@import "Styles/style.scss";

.loadingSpinner {
    display: flex;
    justify-content: center;
    align-items: center;
    .spinner {
        border: 4px solid $grayBackgroundColor;
        border-top: 4px solid $redColor;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        animation: spin 1s linear infinite;
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
